import React, { useState, useEffect } from "react";
import { Drawer, Button } from "antd";
import { MenuOutlined } from "@ant-design/icons";
import "./navbar.scss";
import { Emitter } from "../../utils/emitter";
import I18n from "../../utils/i18n/i18n";
import { Col, Row } from 'antd';
import Logo from '../../assets/logo.png'
import UserInfoComponent from "../user-info";
import SecondMenuComponent from "../second-menu";
import SearchHeaderComponent from "../search-header";

const NavBar = ({ menu }: any) => {
    const [visible, setVisible] = useState(false);

    useEffect(() => {
        Emitter.EventEmitter.addListener(Emitter.Event.Action.MenuClicked, (data: any) => {
            setVisible(false)
        });
    })

    return (<div className="hidden-desktop">
        <Row justify='space-between' gutter={20} align="middle" className="navbar" >
            <Col xs={{ span: 2 }} sm={{ span: 2 }} className="text-left">
                <Button
                    className="menu text-white text-left"
                    type="ghost"
                    icon={<MenuOutlined />}
                    onClick={() => setVisible(true)}
                />
            </Col>
            <Col xs={{ span: 15 }} sm={{ span: 15 }}>
                <SearchHeaderComponent />
            </Col>
            <Col xs={{ span: 3 }} sm={{ span: 3 }} className="text-left">
                <SecondMenuComponent />
            </Col>
            {/* <a href="/"><img src={logo} className="logo" alt="logo" /></a>      */}
        </Row>
        <Drawer
            title={false}
            placement="left"
            width={250}
            // onClick={() => setVisible(false)}
            onClose={() => setVisible(false)}
            open={visible}
            headerStyle={{ display: "none" }}
            className="bg-tuknuk-menu"
        >
            <Row align="top" justify="center" gutter={10} className="margin-menu" >
                <Col>
                    <img src={Logo} height="30" />
                </Col>
            </Row>
            <UserInfoComponent />
            {menu}
            <I18n />
        </Drawer>
    </div>);
};

export default NavBar;