import { Routes, Route, Navigate, Outlet, useNavigate } from "react-router-dom"
import Main from "./components/main";
import { BankingPage } from "./pages/banking";
import { CashierPage } from "./pages/cashier";
import { CRMPage } from "./pages/crm";
import { DashboardPage } from "./pages/dashboard";
import Login from "./pages/login";
import NotFoundPage from "./pages/not-found";
import PermissionDeniedPage from "./pages/permission-denied";
import { ProfilePage } from "./pages/profile";
import { RecordsPage } from "./pages/records";
import { UsersPage } from "./pages/users";
import { SettingsPage } from "./pages/settings";
import { SRMPage } from "./pages/srm";
import { Roles } from "./utils/roles";
import { ProtectedRoutes, PublicRoutes } from "./utils/route";
import LoginPage from "./pages/login";

const MainRoutes = () => (
    <Routes>
        {/** Protected Routes */}
        {/** Wrap all Route under ProtectedRoutes element 
        <Route path='*' element={<Navigate replace to="/not-found" />} />*/}
        <Route path='/not-found' element={<NotFoundPage />} />

        <Route path="/" element={<ProtectedRoutes />}>
            <Route path="/" element={<Main />}>
                <Route path="/" element={<Navigate replace to="dashboard" />} />
                <Route path="dashboard" element={<DashboardPage />} />
                <Route path="dashboard/cashier" element={<CashierPage />} />
                <Route path="dashboard/profile" element={<ProfilePage />} />
                <Route path="dashboard/crm" element={<CRMPage />} />
                <Route path="dashboard/srm" element={<SRMPage />} />
                <Route path="dashboard/settings" element={<SettingsPage />} />
                <Route path="dashboard/banking" element={<BankingPage />} />
                <Route path="dashboard/records" element={<RecordsPage />} />
                <Route path="dashboard/admin" element={<ProtectedRoutes roleRequired={[Roles.admin]} />}>
                    <Route path="users" element={<UsersPage />} />
                </Route>
                {/*
                <Route path="dashboard/records" element={<RecordsPage />} /> 
                <Route path="tabs" element={<Tabs props={{ userName: "Bikash web" }} />}>
                    <Route path="/tabs" element={<Navigate replace to="tab1" />} />
                    <Route path="tab1" element={<Tab1 />} />
                    <Route path="tab2" element={<ProtectedRoutes roleRequired="USER" />}>
                        <Route path="/tabs/tab2" element={<Tab2 />} />
                    </Route>
                    <Route path="tab3" element={<Tab3 />} />
                </Route> */}
            </Route>
        </Route>

        {/** Public Routes */}
        {/** Wrap all Route under PublicRoutes element */}
        <Route path="login" element={<PublicRoutes />}>
            <Route path="/login" element={<LoginPage />} />
        </Route>

        {/** Permission denied route */}
        <Route path="/denied" element={<PermissionDeniedPage />} />
    </Routes>
)

export default MainRoutes