import { Card, Tabs, Col, Row } from 'antd';

export interface ICardInfoWithIconComponent {
    title: string;
    value: string;
    valueExtra?: string;
    valueExtraClass?: string;
    icon: any;
}

export default function CardInfoWithIconComponent(data: ICardInfoWithIconComponent) {
    return (<Card className="bg-tuknuk-menu text-white border-none card-tuknuk">
        <Row align="middle" justify="space-between">
            <Col>
                <p className="card-tuknuk-label">{data.title}</p>
                <p className="card-tuknuk-value">{data.value}
                    {data.valueExtra &&
                        <span className={'card-tuknuk-span text-bold margin-left-small ' + data.valueExtraClass }>{data.valueExtra}</span>
                    }
                </p>
            </Col>
            <Col>
                <div className="card-tuknuk-icon">
                    {data.icon}
                </div>
            </Col>
        </Row>
    </Card>)
}