import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Card, Button, Divider, Col, Input, Row } from 'antd';

export default function LoginPage() {
    const navigate = useNavigate();

    return (
        <div className='full-screen-overlay bg-tuknuk-content '>
            <div className="centered adaptive">
                <Card className="bg-tuknuk-menu text-white border-none card-tuknuk text-center">
                    <Row justify="center" gutter={20} >
                        <h1>Login</h1>
                        <Divider />
                        <Col className="margin-bottom-md" span={24}>
                            <div>Usuario</div>
                            <Input className="input-tuknuk border-none" />
                        </Col>
                        <Col className="margin-bottom-md" span={24}>
                            <div>Senha</div>
                            <Input className="input-tuknuk border-none" />
                        </Col>
                    </Row>
                    <Row align="top" justify="center" gutter={10} >
                        <Col>
                            <Button size='large' type="primary" onClick={() => navigate("/dashboard")}> Acessar </Button>
                        </Col>
                    </Row>
                </Card>
            </div>
        </div>
    );
}