import { AutoComplete, Input } from 'antd';
import { SearchOutlined } from '@ant-design/icons';

export default function SearchHeaderComponent() {
    let options = [
        { value: 'Alterar senha' },
        { value: 'Empresas' },
        { value: 'Unidades' },
        { value: 'Categorias' },
    ].sort((a: any, b: any) => a.value == b.value ? 0 : a.value > b.value ? 1 : -1)
        .map((exemple: any) => exemple)

    const onSelect = (selected: string) => {
        console.log('onSelected', { selected });
    };

    return (
        <AutoComplete
            style={{ width: "100%" }}
            options={options}
            onSelect={onSelect}
            filterOption={(inputValue, option) =>
                option!.value.toUpperCase().indexOf(inputValue.toUpperCase()) !== -1
            }
        >
            <Input className="input-tuknuk border-none" placeholder="Buscar" suffix={<SearchOutlined style={{ fontSize: 20, color: "white" }} />} />
        </AutoComplete>
    )
}