import React, { useState, useEffect } from "react";
import { Layout } from "antd";
import NavBar from '../../components/navbar';
import SideBar from '../../components/sidebar';
import { Emitter } from "../../utils/emitter";
import { Outlet, useLocation, useNavigate } from "react-router-dom";
import { MenuItens } from "./itens";
import { Menu } from "antd";
import { Trans, useTranslation } from "react-i18next";
import { Col, Row, Dropdown, Input, Space, MenuProps } from 'antd';
import { SettingOutlined, SearchOutlined } from '@ant-design/icons';
import SecondMenuComponent from "../second-menu";
import SearchHeaderComponent from "../search-header";
import ChangePasswordComponent from "../change-password";
import TermsAndConditionsComponent from "../terms-and-conditions";
import PrivacyPolicyComponent from "../privacy-policy";

function Main() {
    const navigate = useNavigate();
    const location = useLocation();
    const [selectedKey, setSelectedKey] = useState(location.pathname);
    const [title, setTitle] = useState('');

    const handleClick = (item: any) => {
        Emitter.EventEmitter.emit(Emitter.Event.Action.MenuClicked, true);
        setSelectedKey(item.key);
        navigate(item.key);
    };

    useEffect(() => {
        Emitter.EventEmitter.addListener(Emitter.Event.Action.SetHeaderTitle, ((data: any) => {
            console.log({ data })
            setTitle(data)
        }));
    })

    const { t } = useTranslation();
    const MenuCompomnent = <div className="sidebar-menu">
        {
            MenuItens.map((item, index) => <div key={index} className={`text-white clickable menu-item ${(selectedKey === item.key) ? "active" : ""}`} onClick={() => handleClick(item)}>
                {t(item.label)}
            </div>)
        }
    </div>

    return (
        <div className="App">
            <NavBar menu={MenuCompomnent} />
            <Layout>
                <SideBar menu={MenuCompomnent} />
                <Layout.Content className="content content-responsive bg-tuknuk-content text-white">
                    <Row justify='space-between' gutter={20} align="middle" >
                        <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                            <h1 style={{ fontSize: 35 }}>{title}</h1>
                        </Col>
                        <Col className="margin-bottom-md hidden-tablet-and-mobile" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                            <Row align="middle" gutter={20}>
                                <Col lg={{ span: 20 }}>
                                    <SearchHeaderComponent />
                                </Col>
                                <Col lg={{ span: 4 }}>
                                    <SecondMenuComponent />
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                    <Outlet />
                </Layout.Content>
            </Layout>
            <ChangePasswordComponent />
            <PrivacyPolicyComponent />
            <TermsAndConditionsComponent />
        </div>
    );

}

export default Main;