import { Trans, useTranslation } from "react-i18next";
import { Emitter } from "../../utils/emitter";
import { Table, Tabs, Col, Row, Button, Input, Divider, Tag, Space } from 'antd';
import { CompaniesComponent } from "../../components/companies";
import { UnitsComponent } from "../../components/units";

export function SettingsPage() {
    Emitter.EventEmitter.emit(Emitter.Event.Action.SetHeaderTitle, useTranslation().t("sidebar.settings"));


    return (
        <div className='dashboard'>
            <h1>
                {/* <Trans i18nKey="settings.welcome" /> */}
            </h1>
            <div>
                <Tabs
                    defaultActiveKey="1"
                    className="text-white"
                    items={[
                        {
                            label: 'Empresa',
                            key: '1',
                            children: <div>
                                <Row>
                                    <Col span={24}>
                                        <CompaniesComponent />
                                    </Col>
                                </Row>
                            </div>,
                        },
                        {
                            label: 'Unidades',
                            key: '2',
                            children: <div>
                                <Row>
                                    <Col span={24}>
                                        <UnitsComponent />
                                    </Col>
                                </Row>
                            </div>,
                        },
                        {
                            label: 'Formas de Pagamento',
                            key: '3',
                            children: 'Tab Fechar Caixa',
                        },
                        {
                            label: 'Cash Back',
                            key: '4',
                            children: 'Tab Pagamentos',
                        },
                        {
                            label: 'Categorias',
                            key: '5',
                            children: 'Tab Lista de Categorias',
                        },
                        {
                            label: 'Funcionrios',
                            key: '7',
                            children: 'Tab Lista de Funcionrios',
                        },
                        {
                            label: 'Convites',
                            key: '8',
                            children: 'Tab Lista de Convites',
                        },
                    ]}
                />
            </div>
        </div>
    );

}