import { useEffect, useState } from 'react';
import { Table, Space, Tag, Modal } from 'antd';
import type { ColumnsType } from 'antd/es/table';
import CompanyComponent from '../company';
import { LoadingComponent } from '../loading';

export function CompaniesComponent() {

    const [isModalOpen, setIsModalOpen] = useState(false);

    const showModal = () => {
        setIsModalOpen(true);
    };

    const handleOk = () => {
        setIsModalOpen(false);
    };

    const handleCancel = () => {
        setIsModalOpen(false);
    };


    const dataSource = [
        {
            key: '1',
            name: 'Tuknuk Plataforma de Negcios LTDA',
            age: "42.421.098/0001-09",
            address: 'TukNuk',
            status: true,
            tags: ['active'],
        },
        {
            key: '2',
            name: 'Exemplo LTDA',
            age: "60.701.190/0001-04",
            address: 'Exemplo',
            status: true,
            tags: ['inactive'],
        },
    ];

    const columns: ColumnsType<any> = [
        {
            title: 'Razo social',
            dataIndex: 'name',
            key: 'name',
            responsive: ['md'],
        },
        {
            title: 'CNPJ',
            dataIndex: 'age',
            key: 'age',
            responsive: ['md'],
        },
        {
            title: 'Nome Fantasia',
            dataIndex: 'address',
            key: 'address',
        },
        {
            title: 'Status',
            key: 'function',
            dataIndex: 'function',
            render: (_: any, { tags }: any) => (
                <>
                    {tags.map((tag: any) => {
                        let color = tag.length > 5 ? 'geekblue' : 'green';
                        if (tag === 'active') {
                            color = 'green';
                        }
                        return (
                            <Tag color={color} key={tag}>
                                {tag.toUpperCase()}
                            </Tag>
                        );
                    })}
                </>
            ),
        },
        {
            title: 'Funo',
            key: 'function',
            dataIndex: 'function',
            render: (_: any, { tags }: any) => (
                <>
                    {
                        <Space size="middle" onClick={showModal}>
                            <a>Visualizar</a>
                        </Space>
                    }
                </>
            ),
        },
    ];

    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const timer = setTimeout(() => {
            setLoading(false);
        }, 500);

        return () => clearTimeout(timer);
    }, []);

    return (<>
        {loading && <div className="text-center">
            <LoadingComponent style={{ fontSize: 50, color: "white" }} />
        </div>}
        {!loading && <Table dataSource={dataSource} columns={columns} pagination={false} />}
        <Modal title="Empresa" open={isModalOpen} footer={null} className="modal-adaptive-size" centered={true} maskClosable={false} onCancel={handleCancel}>
            <CompanyComponent />
        </Modal>
    </>)
}