import React, { useState, useEffect } from "react";
import { Button, Row, Col, Input, Modal } from 'antd';
import { Emitter } from "../../utils/emitter";

export default function ChangePasswordComponent() {
    const [isModalOpen, setIsModalOpen] = useState(false);

    const showModal = () => {
        setIsModalOpen(true);
    };

    const handleCancel = () => {
        setIsModalOpen(false);
    };

    useEffect(() => {
        Emitter.EventEmitter.addListener(Emitter.Event.Modal.ChangePassword, (data: boolean) => (data) ? showModal() : handleCancel());
    })

    return (
        <Modal title="Alterar a sua senha" open={isModalOpen} footer={null} centered={true} maskClosable={true} onCancel={handleCancel}>
            <Row justify="start" gutter={20} >
                <Col className="margin-bottom-md" span={24}>
                    <div>Senha atual</div>
                    <Input placeholder='Digite sua senha atual' />
                </Col>
                <Col className="margin-bottom-md" span={24}>
                    <div>Nova senha</div>
                    <Input placeholder='Digite a senha que deseja' />
                </Col>
                <Col className="margin-bottom-md" span={24}>
                    <div>Confirme a sua nova senha</div>
                    <Input placeholder='Digite a senha que deseja' />
                </Col>
            </Row>
            <Row justify="end">
                <Button type="primary">Salvar</Button>
            </Row>
        </Modal>
    )
}