import { Col, Row, Dropdown, Input, Space, MenuProps } from 'antd';
import { SettingOutlined, SearchOutlined } from '@ant-design/icons';
import { Emitter } from '../../utils/emitter';
import { useNavigate } from 'react-router-dom';

export default function SecondMenuComponent() {
    const navigate = useNavigate();

    const items: MenuProps['items'] = [
        {
            key: '0',
            label: 'Alterar Senha',
            onClick: () => {
                Emitter.EventEmitter.emit(Emitter.Event.Modal.ChangePassword, true);
            }
        },
        {
            type: 'divider',
        },
        {
            key: '1',
            label: 'Termos de Uso',
            onClick: () => {
                Emitter.EventEmitter.emit(Emitter.Event.Modal.TermsAndConditions, true);
            }
        },
        {
            key: '2',
            label: 'Poltica de Privacidade',
            onClick: () => {
                Emitter.EventEmitter.emit(Emitter.Event.Modal.PrivacyPolicy, true);
            }
        },
        {
            type: 'divider',
        },
        {
            key: '0',
            label: 'Log out',
            onClick: () => {
                navigate("/login")
            }
        },
    ];

    return (<Dropdown menu={{ items }}>
        <a onClick={(e) => e.preventDefault()}>
            <Space>
                <SettingOutlined style={{ fontSize: '24px', color: 'white' }} />
            </Space>
        </a>
    </Dropdown>)
}