import React, { useState, useEffect } from "react";
import { Layout } from "antd";
import NavBar from '../../components/navbar';
import SideBar from '../../components/sidebar';
import { Emitter } from "../../utils/emitter";
import { Trans, useTranslation } from "react-i18next";
import { Card, Tabs, Col, Row } from 'antd';
import { PlusCircleOutlined, LineChartOutlined, RiseOutlined, ShoppingCartOutlined } from '@ant-design/icons';
import CardInfoWithIconComponent, { ICardInfoWithIconComponent } from "../../components/card-info-with-icon";
import { LoadingComponent } from "../../components/loading";

export function DashboardPage() {
    Emitter.EventEmitter.emit(Emitter.Event.Action.SetHeaderTitle, useTranslation().t("sidebar.dashboard"));
    const cards: ICardInfoWithIconComponent[] = [
        {
            title: "Faturamento do Dia",
            value: "R$ 10.000",
            valueExtra: "+ 10%",
            valueExtraClass: "success-color",
            icon: <PlusCircleOutlined style={{ fontSize: 40, color: "white" }} />
        },
        {
            title: "Checkin de Clientes",
            value: "109",
            valueExtra: "+ 75%",
            valueExtraClass: "success-color",
            icon: <LineChartOutlined style={{ fontSize: 40, color: "white" }} />
        },
        {
            title: "Ticket Mdio",
            value: "300",
            valueExtra: "+ 123%",
            valueExtraClass: "success-color",
            icon: <RiseOutlined style={{ fontSize: 40, color: "white" }} />
        },
        {
            title: "Total de vendas",
            value: "R$ 120.000",
            valueExtra: "+ 654%",
            valueExtraClass: "success-color",
            icon: <ShoppingCartOutlined style={{ fontSize: 40, color: "white" }} />
        }
    ]

    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const timer = setTimeout(() => {
            setLoading(false);
        }, 2000);

        return () => clearTimeout(timer);
    }, []);

    return (
        <div>
            {/* <h1>
                <Trans i18nKey="dashboard.welcome" />
            </h1> */}
            <div>
                <Tabs
                    defaultActiveKey="1"
                    className="text-white"
                    items={[
                        {
                            label: 'Viso geral',
                            key: '1',
                            children: <div>
                                <Row align="top" justify="start" gutter={[8, 16]} wrap={true} >
                                    {
                                        cards.map((card, index) =>
                                            <Col className="margin-bottom-md" key={index} xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                                                {loading && <div className="text-center">
                                                    <LoadingComponent style={{ fontSize: 50, color: "white" }} />
                                                </div>}
                                                {!loading && <CardInfoWithIconComponent
                                                    title={card.title}
                                                    value={card.value}
                                                    valueExtra={card.valueExtra}
                                                    valueExtraClass={card.valueExtraClass}
                                                    icon={card.icon}
                                                />}
                                            </Col>
                                        )
                                    }
                                </Row>
                            </div>,
                        },
                        {
                            label: 'Credenciar Unidades',
                            key: '2',
                            children: <div>
                                {
                                    [...Array(100)].map((el, index) => <h1 key={index}> Line {index + 1} </h1>)
                                }
                            </div>,
                        },
                        {
                            label: 'Perfis de Acesso',
                            key: '3',
                            children: 'Tab 3',
                        },
                        {
                            label: 'Usurios',
                            key: '4',
                            children: 'Tab de Usurios',
                        },
                    ]}
                />
            </div>
        </div>
    );

}