import { Col, Row } from 'antd';
import PlaceImg from '../../assets/place.jpg'

export default function UserInfoComponent() {
    return (<>
        <Row align="top" justify="center" className="margin-menu">
            <Col>
                <img src={PlaceImg} height="120" width="120" className='round-50-cover' />
            </Col>
        </Row>
        <Row align="top" justify="center" className='text-center text-white'>
            <Col span={24}>
                <div className='title-adaptive'>
                    Lucca Cafs Especiais
                </div>
            </Col>
            <Col span={24}>
                <h5>Matriz</h5>
            </Col>
        </Row>
        <hr className='divisor primary-color margin-bottom-sm' />
    </>)
}