
import { Col, Row, Button, Input, Divider } from 'antd';

export function UnitComponent() {
    return (
        <div className='dashboard'>
            <Row justify="start" gutter={20} >
                <h1>Dados bsicos</h1>
                <Divider />
                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                    <div>Razo social</div>
                    <Input placeholder='Razo social' />
                </Col>
                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                    <div>CNPJ</div>
                    <Input placeholder='CNPJ' />
                </Col>
                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                    <div>Nome Fantasia</div>
                    <Input />
                </Col>
            </Row>
            <Row justify="start" gutter={20}>

                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                    <div>Nome do proprietrio</div>
                    <Input />
                </Col>
                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                    <div>CPF do proprietrio</div>
                    <Input />
                </Col>
            </Row>
            <Row justify="start" gutter={20} >
                <h1>Endereo</h1>
                <Divider />
                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                    <div>CEP</div>
                    <Input />
                </Col>
                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                    <div>Logradouro</div>
                    <Input />
                </Col>
                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 3 }}>
                    <div>Nmero</div>
                    <Input />
                </Col>
                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 3 }}>
                    <div>Complemento</div>
                    <Input />
                </Col>
            </Row>
            <Row justify="start" gutter={20} >
                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                    <div>Bairro</div>
                    <Input />
                </Col>
                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                    <div>Cidade</div>
                    <Input />
                </Col>
                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                    <div>UF</div>
                    <Input />
                </Col>
            </Row>
            <Row justify="start" align="middle" gutter={20} >
                <h1>Dados de contato</h1>
            </Row>
            <Divider />
            <Row justify="start" align="middle" gutter={20} >
                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 6 }} lg={{ span: 6 }}>
                    <div>Nome</div>
                    <Input />
                </Col>
                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 6 }} lg={{ span: 6 }}>
                    <div>Cargo</div>
                    <Input />
                </Col>
                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 6 }} lg={{ span: 6 }}>
                    <div>Telefone</div>
                    <Input />
                </Col>
                <Col className="margin-bottom-md text-align-adaptive-size" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 3 }} lg={{ span: 6 }} >
                    <Button className='margin-top-min' type="primary">+</Button>
                </Col>
            </Row>
            <Row justify="end">
                <Button type="primary">Salvar</Button>
            </Row>
        </div>
    );
}