
import { useNavigate } from 'react-router-dom';
import { Button, Col, Row } from 'antd';

export default function NotFoundPage() {
    const navigate = useNavigate();

    return (
        <div className='full-screen-overlay bg-tuknuk-content '>
            <div className="centered">
                <Row align="top" justify="center" gutter={10} >
                    <Col>
                        <h1 className='text-white'>Not Found</h1>
                    </Col>
                </Row>
                <Row align="top" justify="center" gutter={10} >
                    <Col>
                        <Button size='large' type="primary" onClick={() => navigate("/dashboard")}> Voltar para home </Button>
                    </Col>
                </Row>
            </div>
        </div>
    );
}