import { useEffect, useState } from 'react';
import { Col, Row, Button, Input, Divider, Tabs } from 'antd';

export default function CompanyComponent() {
    const [contactForm, setContactForm] = useState([true]);

    function addContactForm() {
        (contactForm.length == 3) ?
            setContactForm([...contactForm.map(() => false), false])
            :
            setContactForm([...contactForm.map(() => false), true])
    }

    return (
        <div className='dashboard'>
            <Tabs
                defaultActiveKey="1"
                items={[
                    {
                        label: 'Dados bsicos',
                        key: '1',
                        children: <div>
                            <Row justify="start" gutter={20} >
                                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                                    <div>Razo social</div>
                                    <Input placeholder='Razo social' />
                                </Col>
                                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                                    <div>CNPJ</div>
                                    <Input placeholder='CNPJ' />
                                </Col>
                                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                                    <div>Nome Fantasia</div>
                                    <Input />
                                </Col>
                            </Row>
                            <Row justify="start" gutter={20}>

                                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                                    <div>Nome do proprietrio</div>
                                    <Input />
                                </Col>
                                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                                    <div>CPF do proprietrio</div>
                                    <Input />
                                </Col>
                            </Row>
                        </div>,
                    },
                    {
                        label: 'Endereo',
                        key: '2',
                        children: <div>
                            <Row justify="start" gutter={20} >
                                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                                    <div>CEP</div>
                                    <Input />
                                </Col>
                                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                                    <div>Logradouro</div>
                                    <Input />
                                </Col>
                                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 3 }}>
                                    <div>Nmero</div>
                                    <Input />
                                </Col>
                                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 3 }}>
                                    <div>Complemento</div>
                                    <Input />
                                </Col>
                            </Row>
                            <Row justify="start" gutter={20} >
                                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                                    <div>Bairro</div>
                                    <Input />
                                </Col>
                                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                                    <div>Cidade</div>
                                    <Input />
                                </Col>
                                <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                                    <div>UF</div>
                                    <Input />
                                </Col>
                            </Row>
                        </div>,
                    },
                    {
                        label: 'Dados de contato',
                        key: '3',
                        children: <div>
                            {
                                contactForm.map((el, index) => <Row key={index} justify="start" align="middle" gutter={20} >
                                    <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 6 }} lg={{ span: 6 }}>
                                        <div>Nome</div>
                                        <Input />
                                    </Col>
                                    <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 6 }} lg={{ span: 6 }}>
                                        <div>Cargo</div>
                                        <Input />
                                    </Col>
                                    <Col className="margin-bottom-md" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 6 }} lg={{ span: 6 }}>
                                        <div>Telefone {contactForm.length === (index + 1)}</div>
                                        <Input />
                                    </Col>
                                    <Col className={`margin-bottom-md text-align-adaptive-size ${(el) ? "" : "hidden"}`} xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 3 }} lg={{ span: 6 }} >
                                        <Button className='margin-top-min' type="primary" onClick={addContactForm}>+</Button>
                                    </Col>
                                </Row>)
                            }
                        </div>,
                    },
                ]}
            />
            <Row justify="center" style={{ marginTop: "15px" }}>
                <Button type="primary">Salvar</Button>
            </Row>
        </div>
    );
}