import React, { useState, useEffect } from "react";
import { Row, Col, Modal } from 'antd';
import { Emitter } from "../../utils/emitter";

export default function PrivacyPolicyComponent() {
    const [isModalOpen, setIsModalOpen] = useState(false);

    const showModal = () => {
        setIsModalOpen(true);
    };

    const handleCancel = () => {
        setIsModalOpen(false);
    };

    useEffect(() => {
        Emitter.EventEmitter.addListener(Emitter.Event.Modal.PrivacyPolicy, (data: boolean) => (data) ? showModal() : handleCancel());
    })

    return (
        <Modal title="Poltica de Privacidade" open={isModalOpen} footer={null} centered={true} maskClosable={true} onCancel={handleCancel}>
            <Row justify="start" gutter={20} >
                <Col className="margin-bottom-md" span={24}>
                    <div>
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Nec tincidunt praesent semper feugiat nibh. Sit amet consectetur adipiscing elit pellentesque habitant. Libero volutpat sed cras ornare arcu dui. Egestas quis ipsum suspendisse ultrices gravida dictum fusce. Maecenas ultricies mi eget mauris. Viverra orci sagittis eu volutpat. Ullamcorper velit sed ullamcorper morbi. Pulvinar proin gravida hendrerit lectus a. Tellus elementum sagittis vitae et leo duis ut diam quam. Interdum velit laoreet id donec ultrices. Nunc lobortis mattis aliquam faucibus purus in massa tempor. Nec feugiat in fermentum posuere urna nec tincidunt praesent. Id venenatis a condimentum vitae. Volutpat lacus laoreet non curabitur.
                    </div>
                </Col>
                <Col className="margin-bottom-md" span={24}>
                    <div>
                        Cum sociis natoque penatibus et magnis. Lorem dolor sed viverra ipsum nunc aliquet. Velit aliquet sagittis id consectetur purus ut faucibus. Lacus viverra vitae congue eu consequat. Adipiscing enim eu turpis egestas pretium. Sed enim ut sem viverra aliquet eget sit amet. Eu facilisis sed odio morbi. In nibh mauris cursus mattis molestie a iaculis at. Mauris commodo quis imperdiet massa tincidunt nunc pulvinar sapien. Lorem ipsum dolor sit amet.
                    </div>
                </Col>
            </Row>
        </Modal>
    )
}